.MainContainer {
    background-color: #f5f5f5;
    padding-left: 90px;
    padding-right: 62px;
}

.HeadingContainer {

}

.IconContainer {
    padding-top: 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 50%;
}

.Twitter {
    padding-right: 18px;
}

.TextInfoContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 35px;
    padding-bottom: 35px;
    overflow: hidden;
    align-items: center;
}

.Heading {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    color: #1d2434;
}

.SubHeading {
    width: 429px;
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    letter-spacing: 0.01em;
    color: #1d2434;
    padding-top: 12px;
    cursor: pointer;
}

.Line {
    width: 100%;
    height: 0px;
    left: 91px;
    top: 351px;
    border-bottom: 1px solid rgba(29, 36, 52, 0.5);
    margin-top: 30px;
}

.CopyRightContainer {
    font-family: 'Mulish';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    /* identical to box height, or 26px */

    letter-spacing: 0.01em;

    /* b/70 */

    color: rgba(29, 36, 52, 0.7);
    margin-top: 22px;
    padding-bottom: 22px;
}

.Cursor {
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .TextInfoContainer {
        flex-direction: column;
    }
    .Services,
    .Address,
    .Contact {
        padding: 1rem 0;
    }
    .MainContainer {
        padding-left: 2rem;
    }
}
